html {
  font-family: "Roboto", sans-serif;
  line-height: 1.42;
  font-size: 16px;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  height: 100%;
  color: #2A2D33;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.42;
  background: #FFF;
}

input::-ms-clear {
  display: none;
}

/*  Safari */
input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

p {
  margin: 0px 0px 24px;
}

ul, ol {
  padding: 0px;
  margin: 0px;
  list-style: none;
}

li {
  list-style: none;
}

a {
  color: #2A2D33;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6, .h20 {
  font-family: "Roboto";
  font-weight: 700;
  margin: 0px 0px 32px;
  text-transform: none;
  line-height: 1.2;
}

h1, .h1 {
  font-size: 60px;
  font-size: 3.75rem;
  margin-top: 0;
  line-height: 1.15;
}

h2, .h2, .ui-h2 {
  font-size: 48px;
  font-size: 3rem;
  line-height: 1.2;
}

h3, .h3, .ui-h3 {
  font-size: 32px;
  font-size: 2rem;
  line-height: 1.2;
}

h4, .h4, .ui-h4 {
  font-size: 24px;
  font-size: 1.5rem;
}

h5, .h5 {
  font-size: 20px;
  font-size: 1.25rem;
}

h6, .h6, .ui-h5 {
  font-size: 18px;
  font-size: 1.125rem;
  text-transform: none;
  font-weight: 700;
}

.ui-font-midi {
  font-size: 16px;
  font-size: 1rem;
}

.ui-font-mini {
  font-size: 14px;
  font-size: 0.875rem;
}

.h20 {
  font-size: 20px;
  font-size: 1.25rem;
}

.ui-strong,
.ui-bold {
  font-weight: 700;
}

.ui-demi {
  font-weight: 600;
}

a {
  text-decoration: none;
  outline: none;
  transition: color 0.3s, border 0.3s;
}

b, strong {
  font-weight: 700;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper a img {
  border: none;
  display: block;
  line-height: 0;
  font-size: 0;
}

