.ui-btn-main {
  width: max-content;
  padding-left: 40px;
  padding-right: 40px;
  background-color: #2A2D33;
  color: #fff;
  font-size: 18px;
  font-size: 1.125rem;
  height: 80px;
  line-height: 80px;
  letter-spacing: 2.7px;
  letter-spacing: 0.16875rem;
  text-transform: uppercase;
  border-radius: 51px;
  cursor: pointer;
  border: 1px solid #2A2D33;
  outline: none;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  display: inline-block;
}
@media (max-width: 768px) {
  .ui-btn-main {
    letter-spacing: 2.4px;
    letter-spacing: 0.15rem;
    font-size: 16px;
    font-size: 1rem;
    height: 72px;
    line-height: 72px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 480px) {
  .ui-btn-main {
    width: 100%;
  }
}
@media (max-width: 350px) {
  .ui-btn-main {
    letter-spacing: 1.6px;
    letter-spacing: 0.1rem;
    font-size: 14px;
    font-size: 0.875rem;
    height: 68px;
