.ui-field {
  position: relative;
  height: 38px;
}
.ui-field label.error {
  position: absolute;
  bottom: -20px;
  top: 100%;
  left: 0;
  color: #B10000;
  font-size: 12px;
  font-size: 0.75rem;
  width: max-content;
}
.ui-field .ui-star {
  position: absolute;
  left: 10px;
  font-size: 18px;
  font-size: 1.125rem;
  cursor: text;
  z-index: 0;
  pointer-events: none;
}
.ui-field input:invalid ~ .ui-star::before {
  content: attr(placeholder);
  position: relative;
  margin-right: 4px;
}
.ui-field input:invalid ~ .ui-star::after {
  content: "*";
  position: relative;
  color: #B10000;
}
.ui-field textarea:invalid ~ .ui-star::before {
  content: attr(placeholder);
  position: relative;
  margin-right: 4px;
}
.ui-field textarea:invalid ~ .ui-star::after {
  content: "*";
  position: relative;
  color: #B10000;
}

