  display: none;
}
@media (max-width: 960px) {
  .burger-btn {
    display: flex;
    z-index: 1;
  }
}
.burger-btn span {
  display: block;
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translate(0%, -50%);
  height: 2px;
  width: 100%;
  border-radius: 2px;
  background-color: #2A2D33;
  opacity: 0.6;
}
.burger-btn span:before, .burger-btn span:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 2px;
  background-color: #2A2D33;
  position: absolute;
  left: 0;
  transition: transform 0.2s ease-in-out;
}
.burger-btn span:before {
  top: -7px;
}
.burger-btn span:after {
  top: 7px;
}
.burger-btn span.active {
  transform: rotate(45deg);
  background-color: #2A2D33;
  opacity: 1;
}
.burger-btn span.active:before {
  top: 0;
  transform: rotate(-90deg);
  background-color: #2A2D33;
  opacity: 1;
}
.burger-btn span.active:after {
  top: 0;
  transform: rotate(90deg);
  background-color: #2A2D33;
  opacity: 1;
}
.burger-btn__white span {
  background-color: #fff;
}
.burger-btn__white span:before, .burger-btn__white span:after {
  background-color: #fff;
}

.mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 140px;
  padding-bottom: 60px;
  margin-bottom: 100px;
  color: #fff;
  overflow-y: auto;
  z-index: 1;
}
.mobile-menu-wrapper {
  background-color: #fff;
  padding-top: 30px;
  padding-bottom: 60px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
}
@media (max-width: 960px) {
  .mobile-menu {
    display: block;
    transform: translateX(-100%);
    transition: transform 0.3s;
  }
  .mobile-menu.active {
    display: block;
    transform: translateX(0%);
    background-color: rgba(0, 0, 0, 0.4);
  }
}
.mobile-nav ul {
  text-align: center;
}
.mobile-nav li {
  position: relative;
  width: max-content;
  font-size: 18px;
  font-size: 1.125rem;
  text-align: center;
  width: 100%;
}
.mobile-nav li a {
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 8px;
  display: inline-block;
}
.mobile-nav li > ul {
  height: 0;
  width: 0;
  overflow: hidden;
  font-size: 0px;
  font-size: 0rem;
}
.mobile-nav li > ul li {
  font-size: 18px;
  font-size: 1.125rem;
  padding-left: 12px;
}
.mobile-nav li.opened > ul {
  height: max-content;
  width: 100%;
}
.mobile-nav li.opened .icon {
  transform: rotate(180deg);
}
.mobile-nav li .icon {
  display: inline-block;
  vertical-align: middle;
  width: 22px;
  height: 22px;
  fill: none;
  transition: transform 0.3s;
  stroke: #2A2D33;
}